.statistics {
  width: 400px;
  margin: 0 auto;
  text-align: center;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.title {
  font-size: 24px;
  font-weight: bold;
  padding: 20px 0;
  background-color: #f3f6f9;
}

.statList {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}

.item {
  flex-basis: 25%;
  padding: 20px;
  color: #fff;
  text-align: center;
}

.label {
  display: block;
  font-size: 14px;
}

.percentage {
  font-size: 20px;
  font-weight: bold;
}
